import React, { useState, CSSProperties } from 'react'
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { CircleSpinnerOverlay } from 'react-spinner-overlay'
import WordCloud from 'react-d3-cloud';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 500,
  },
  paper: {
    margin: theme.spacing(3),
    padding: theme.spacing(2),
  },
  container: {
    height: "100%", // So that grids 1 & 4 go all the way down
    minHeight: 150, // Give minimum height to a div
    border: "1px solid #17A2B8",
    fontSize: 30,
    textAlign: "center",
    borderRadius: "6px"

  },
  containerTall: {
    minHeight: 250 // This div has higher minimum height
  },
  result: {
    borderRadius: "6px"
  }
}));


function Home() {
  const classes = useStyles();

  const [url, setURL] = useState('')
  const [sentiment, setSentiment] = useState({ total: 0, neutral: [], positive: [], negative: [] })
  const [loading, setLoading] = useState(false)
  const [wordCloud, setWordCloud] = useState([])

  function getStopWords() {
    return [`i`, `me`, `my`, `myself`, `we`, `our`, `ours`, `ourselves`, `you`, `your`, `yours`, `yourself`, `yourselves`, `he`, `him`, `his`, `himself`, `she`, `her`, `hers`, `herself`, `it`, `its`, `itself`, `they`, `them`, `their`, `theirs`, `themselves`, `what`, `which`, `who`, `whom`, `this`, `that`, `these`, `those`, `am`, `is`, `are`, `was`, `were`, `be`, `been`, `being`, `have`, `has`, `had`, `having`, `do`, `does`, `did`, `doing`, `a`, `an`, `the`, `and`, `but`, `if`, `or`, `because`, `as`, `until`, `while`, `of`, `at`, `by`, `for`, `with`, `about`, `against`, `between`, `into`, `through`, `during`, `before`, `after`, `above`, `below`, `to`, `from`, `up`, `down`, `in`, `out`, `on`, `off`, `over`, `under`, `again`, `further`, `then`, `once`, `here`, `there`, `when`, `where`, `why`, `how`, `all`, `any`, `both`, `each`, `few`, `more`, `most`, `other`, `some`, `such`, `no`, `nor`, `not`, `only`, `own`, `same`, `so`, `than`, `too`, `very`, `s`, `t`, `can`, `will`, `just`, `don`, `should`, `now`]
  }

  const analysis = async () => {

    if (!url) {
      alert(`the url invalid please try again!`)
      return;
    }
    if (!/youtube.com/ig.test(url)) {
      alert(`the url invalid please try again!`)
      return;
    }

    const baseUrl = `https://social-comments-analysis-fn.azurewebsites.net/`;
    const api = `api/getYoutubeCommentsFn?url=${url}`;


    setLoading(true)
    var data = await fetch(baseUrl + api).then(data => data.json())

    setLoading(false)

    setSentiment(data);

    var words = [].concat(data.negative).concat(data.positive).concat(data.neutral).map(x => x.text).join(' ')
      .split(" ");

    // count word when same word
    var counts = words.reduce((acc, curr) => {
      curr = curr.toLowerCase()
      if (getStopWords().includes(curr)) {
        return acc;
      }
      if (typeof acc[curr] == 'undefined') {
        acc[curr] = 1;
      } else {
        acc[curr] += 1;
      }

      return acc;
    }, {});

    // convert counts to array with text and count properties
    var wc = Object.keys(counts).map(function (key) {
      return {
        text: key,
        value: counts[key]
      };
    });

    // sort array by count property
    wc.sort(function (a, b) {

      return b.value - a.value;
    });

    // get top 200 item of wc

    var top500wc = wc.slice(0, 300);


    console.log(top500wc)

    setWordCloud(top500wc)
  }

  return (
    <div>
      <CircleSpinnerOverlay
        loading={loading}
        overlayColor="rgba(108,117,125,0.5)"
        color='#007BFF'
      />
      <Container maxWidth="md">
        <Paper className={classes.paper}>
          <Grid item md={true} sm={true} xs={true}>
            <TextField id="url" value={url} label="INPUT YOUTUBE VIDEO URL" type="text" onChange={e => setURL(e.target.value)} margin="normal" fullWidth autoFocus required />
          </Grid>
          <Grid item md={true} sm={true} xs={true}>
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center"
              }}
            >
              <Button variant="contained" color="primary" onClick={analysis}>
                Go!
              </Button>
            </div>
          </Grid>
        </Paper>
        {sentiment.total > 0 &&
          (
            <Paper className={classes.paper}>

              <Typography variant="h4" align="center" gutterBottom  >
                Sentiment Analysis
              </Typography>
              <Grid container direction="row" spacing={2}>
                <Grid item xs className={classes.result} >
                  <div className={classes.container} >positive
                    {sentiment.positive.length > 0 && (
                      <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                        {sentiment.positive.map((comment, i) => (
                          <ListItem alignItems="flex-start" key={i} >
                            <ListItemAvatar>
                              <Avatar alt="Remy Sharp" src={comment.authorThumb[0].url} />
                            </ListItemAvatar>
                            <ListItemText
                              primary={comment.author}
                              secondary={
                                <React.Fragment>
                                  <Typography
                                    sx={{ display: 'inline' }}
                                    component="span"
                                    variant="body2"
                                    color="text.primary"
                                  >
                                  </Typography>
                                  {comment.text}
                                </React.Fragment>
                              }
                            />
                          </ListItem>
                        ))}
                      </List>
                    )}
                  </div>
                </Grid>
                <Grid item container direction="column" xs spacing={2}>
                  <Grid item xs>
                    <div className={classes.container}>negative
                      {sentiment.negative.length > 0 && (
                        <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                          {sentiment.negative.map((comment, i) => (
                            <ListItem alignItems="flex-start" key={i} >
                              <ListItemAvatar>
                                <Avatar alt="Remy Sharp" src={comment.authorThumb[0].url} />
                              </ListItemAvatar>
                              <ListItemText
                                primary={comment.author}
                                secondary={
                                  <React.Fragment>
                                    <Typography
                                      sx={{ display: 'inline' }}
                                      component="span"
                                      variant="body2"
                                      color="text.primary"
                                    >
                                    </Typography>
                                    {comment.text}
                                  </React.Fragment>
                                }
                              />
                            </ListItem>
                          ))}
                        </List>
                      )}
                    </div>
                  </Grid>

                </Grid>
                <Grid item xs>
                  <div className={classes.container}>neutral
                    {sentiment.neutral.length > 0 && (
                      <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                        {sentiment.neutral.map((comment, i) => (
                          <ListItem alignItems="flex-start" key={i} >
                            <ListItemAvatar>
                              <Avatar alt="Remy Sharp" src={comment.authorThumb[0].url} />
                            </ListItemAvatar>
                            <ListItemText
                              primary={comment.author}
                              secondary={
                                <React.Fragment>
                                  <Typography
                                    sx={{ display: 'inline' }}
                                    component="span"
                                    variant="body2"
                                    color="text.primary"
                                  >
                                  </Typography>
                                  {comment.text}
                                </React.Fragment>
                              }
                            />
                          </ListItem>
                        ))}
                      </List>
                    )}
                  </div>
                </Grid>
              </Grid>
            </Paper>
          )
        }

        {sentiment.total > 0 &&
          (
            <Paper className={classes.paper}>
              <Typography variant="h4" align="center" gutterBottom  >
                WordCloud
              </Typography>
              <WordCloud data={wordCloud}
                spiral="rectangular"
                width={100}
                height={100}
                fontWeight="bold"
                rotate={() => 0}

              />
            </Paper>
          )
        }
      </Container>
    </div>
  )
}

export default Home
